import { useCallback, useRef, useState } from 'react';
import MonacoEditor from 'react-monaco-editor';
import { Configuration, OpenAIApi } from 'openai';

import {
    Button, CircularProgress, Dialog,
    DialogActions, DialogContent, DialogTitle,
    IconButton, TextField,
} from '@mui/material';

import {
    Check, Close, SmartToy,
    QuestionMark as Question,
    FileCopy as Copy,
} from '@mui/icons-material';

import { Utils, I18n } from '@iobroker/adapter-react-v5';
import { detectDevice, systemPrompt } from './OpenAiPrompt';

const LANGUAGES = {
    ru: 'Russian',
    en: 'English',
    de: 'German',
    es: 'Spanish',
    fr: 'French',
    it: 'Italian',
    pl: 'Polish',
    nl: 'Dutch',
    pt: 'Portuguese',
    uk: 'Ukrainian',
    'zh-cn': 'Chinese',
};

const OpenAiDialog = props => {
    const [question, setQuestion] = useState(window.localStorage.getItem('openai-question') || '');
    const [answer, setAnswer] = useState('');
    const [open, setOpen] = useState(false);
    const [working, setWorking] = useState(false);
    const [showKeyWarning, setShowKeyWarning] = useState(false);
    const devicesCache = useRef(null);
    const gptKeyCache = useRef(null);
    const docsCache = useRef(null);

    const ask = useCallback(async () => {
        let devices;
        if (!devicesCache.current) {
            devices = await detectDevice(props.socket);
            devicesCache.current = devices;
        } else {
            devices = devicesCache.current;
        }
        let apiKey;
        if (!gptKeyCache.current) {
            const config = await props.socket.getObject(`system.adapter.javascript.${props.instance || 0}`);
            apiKey = config.native.gptKey;
            gptKeyCache.current = apiKey;
        } else {
            apiKey = gptKeyCache.current;
        }

        let docs;
        if (!docsCache.current) {
            docs = await systemPrompt();
            docsCache.current = docs;
        } else {
            docs = docsCache.current;
        }
        if (!apiKey) {
            setShowKeyWarning(true);
            return;
        }

        const configuration = new Configuration({ apiKey });
        setWorking(true);
        try {
            const openai = new OpenAIApi(configuration);

            const chatCompletionPhase1 = await openai.createChatCompletion({
                model: 'gpt-3.5-turbo-16k',
                messages: [
                    {
                        role: 'system',
                        content: `You are programmer. Here is a documentation:\n\n${docs}`,
                    },
                    { role: 'system', content: `Here is list of devices:\n\n${JSON.stringify(devices, null, 2)}` },
                    { role: 'user', content: `Write code that do:\n\n${question}\n\nReturn only code. Write comments in ${LANGUAGES[I18n.getLanguage()] || 'English'}` }],
            });
            const message = chatCompletionPhase1.data.choices[0].message;
            const m = message.content.match(/```(javascript|js|typescript)\n?(.*)```(.*)/ms);
            let code;
            if (!m) {
                code = message.content;
                if (code.startsWith('`')) {
                    code = code.substring(1);
                }
                if (code.endsWith('`')) {
                    code = code.substring(0, code.length - 1);
                }
            } else {
                code = m[2];
                if (m[3]) {
                    code = `${m[3].split('\n').map(line => `// ${line.trim()}`).join('\n')}\n${code}`;
                }
            }
            console.log(message);
            setAnswer(code);
        } catch (error) {
            console.error(`Cannot request: ${error}`);
        }

        setWorking(false);
    }, [question]);

    return <>
        <IconButton
            key="ai"
            aria-label="AI"
            title={I18n.t('AI code generator')}
            className={props.classes.toolbarButtons}
            size="medium"
            onClick={() => setOpen(true)}
        >
            <SmartToy />
        </IconButton>
        {showKeyWarning && <Dialog
            maxWidth="lg"
            open={!0}
            onClose={() => setShowKeyWarning(false)}
            fullWidth
        >
            <DialogTitle>{I18n.t('No Chat GPT Key found')}</DialogTitle>
            <DialogContent style={{ display: 'flex', flexDirection: 'column', gap: 8 }}>
                <div>
                    {I18n.t('You have to enter OpenAI API key in the configuration of javascript adapter.')}
                </div>
                <Button
                    variant="contained"
                    onClick={() => {
                        window.open(`../#tab-instances/config/system.adapter.javascript.${props.instance || 0}`, '_blank');
                        setShowKeyWarning(false);
                    }}
                >
                    {I18n.t('Open configuration')}
                </Button>
            </DialogContent>
            <DialogActions>
                <Button
                    variant="contained"
                    startIcon={<Close />}
                    onClick={() => setShowKeyWarning(false)}
                >
                    {I18n.t('Close')}
                </Button>
            </DialogActions>
        </Dialog>}
        {open && <Dialog
            maxWidth="lg"
            open={!0}
            onClose={() => setOpen(false)}
            fullWidth
        >
            <DialogTitle>{I18n.t('AI code generator')}</DialogTitle>
            <DialogContent style={{ display: 'flex', flexDirection: 'column', gap: 8 }}>
                <div>
                    <TextField
                        variant="standard"
                        multiline
                        autoFocus
                        disabled={working}
                        fullWidth
                        onKeyUp={e => {
                            if (e.key === 'Enter' && e.ctrlKey) {
                                ask();
                            }
                        }}
                        label={I18n.t('Enter your question')}
                        helperText={I18n.t('Press Ctrl+Enter to get the answer')}
                        value={question}
                        onChange={e => {
                            window.localStorage.setItem('openai-question', e.target.value);
                            setQuestion(e.target.value);
                        }}
                    />
                </div>
                <div>
                    <Button
                        variant="contained"
                        disabled={working || !question}
                        startIcon={<Question />}
                        onClick={async () => ask()}
                    >
                        {working ? <CircularProgress size={24} /> : I18n.t('Ask')}
                    </Button>
                </div>
                <div>
                    {I18n.t('Result')}
                </div>
                <div>
                    <MonacoEditor
                        width="100%"
                        height="400"
                        languages={['javascript', 'typescript']}
                        language={props.language}
                        theme={props.isDark ? 'vs-dark' : ''}
                        value={answer}
                        options={{ readOnly: true }}
                    />
                </div>
            </DialogContent>
            <DialogActions>
                <Button
                    color="grey"
                    variant="outlined"
                    disabled={!answer}
                    startIcon={<Copy />}
                    onClick={() => {
                        Utils.copyToClipboard(answer);
                        window.alert(I18n.t('Copied'));
                    }}
                >
                    {I18n.t('Copy to clipboard')}
                </Button>
                <Button
                    color="primary"
                    variant="contained"
                    disabled={!answer}
                    startIcon={<Check />}
                    onClick={() => {
                        props.onAddCode(answer);
                        setOpen(false);
                    }}
                >
                    {I18n.t('Use generated code')}
                </Button>
                <Button
                    color="grey"
                    variant="contained"
                    startIcon={<Close />}
                    onClick={() => {
                        setOpen(false);
                    }}
                >
                    {I18n.t('Close')}
                </Button>
            </DialogActions>
        </Dialog>}
    </>;
};

export default OpenAiDialog;
